<template>
    <div id="content" ref="content">
        <button @click="toggleMode" class="switch-mode-button">Switch Mode</button>
        <iframe v-if="mode === 'html'" :src="htmlUrl" id="my-iframe" ref="myIframe"></iframe>
        <div v-else>
            <div v-for="(item, index) in extractedSentences" :key="index">
                <h3>{{ item.keyword }}</h3>
                <p ref="sentenceParagraph">{{ item.sentences || '' }}</p>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';
import axios from 'axios';
import Mark from 'mark.js';

export default {
    name: 'PaperView',
    data() {
        return {
            htmlUrl: '',
            paperHtml: '',
            mode: "html",
            scrollPosition: 0,
            keywords: ["data storytelling", "data story", "narrative visualization", "data-driven storytelling", "storytelling with data", "data stories", "data news", "data-driven news","story","narrative","structure","character","stories","sequence"]
        };
    },
    computed: {
        ...mapState([
            'extractedSentencesData'
        ]),
        extractedSentences() {
            return this.extractedSentencesData.filter(item => item.paperID === this.id);
        }
    },
    props: {
        id: {
            type: String,
            required: true
        }
    },
    watch: {
        id(newId) {
            this.fetchPaper(newId)
        },
        mode(newMode, oldMode) {
            if (oldMode === 'html') {
                this.$el.querySelector('iframe').removeEventListener('load', this.highlightKeywords);
            }
            if (newMode === 'html') {
                // Wait for the next render cycle before adding the event listener 
                this.$nextTick(() => {
                    this.$el.querySelector('iframe').addEventListener('load', this.highlightKeywords);
                });
            }
        },
    },
    methods: {
        toggleMode() {
            this.mode = this.mode === 'html' ? 'extract_sentence' : 'html';
            if (this.mode === 'extract_sentence') {
                // restore scroll position when switching back to sentences mode
                this.$nextTick(() => {
                    this.$refs.content.scrollTop = this.scrollPosition;
                });
            }
        },
        fetchPaper(id) {
            var _this = this;
            console.log(`${id}` + " is clicked");
            // Try to fetch the HTML file
            axios.get(`paperdocs/${id}.html`)
                .then(response => {
                    _this.paperHtml = response.data;
                    // Create a blob URL from the HTML content
                    const blob = new Blob([_this.paperHtml], { type: 'text/html' });
                    _this.htmlUrl = URL.createObjectURL(blob);
                })
                .catch(error => {
                    // If fetching the HTML file fails, try to fetch the PDF file
                    if (error && error.response && error.response.status === 404) {
                        axios.get(`paperdocs/${id}.pdf`, { responseType: 'blob' })
                            .then(response => {
                                const pdfBlob = new Blob([response.data], { type: 'application/pdf' });
                                _this.htmlUrl = URL.createObjectURL(pdfBlob);
                            })
                            .catch(error => {
                                console.error(`Failed to fetch paper ${id}: `, error);
                            });
                    } else {
                        console.error(`Failed to fetch paper ${id}: `, error);
                    }
                });
        },
        highlightKeywords() {
            if (this.mode === 'extract_sentence') {
                this.$nextTick(() => {
                    this.extractedSentences.forEach((item, index) => {
                        const paragraphElement = this.$refs.sentenceParagraph[index];
                        const instance = new Mark(paragraphElement);
                        this.keywords.forEach(function (keyword) {
                            instance.mark(keyword, {
                                diacritics: false,
                                separateWordSearch: false,
                                acrossElements: true,
                                caseSensitive: false
                            });
                        });
                    });
                });
            }
            else {
                // Access the iframe's contentDocument
                const iframeDoc = this.$el.querySelector('iframe').contentDocument;
                const instance = new Mark(iframeDoc.body);
                this.keywords.forEach(function (keyword) {
                    instance.mark(keyword, {
                        diacritics: false,
                        separateWordSearch: false,
                        acrossElements: true,
                        caseSensitive: false
                    });
                });
            }
        },
        handleScroll() {
            if (this.mode === 'extract_sentence') {
                this.scrollPosition = this.$refs.content.scrollTop;
                console.log(this.scrollPosition)
            }
        },
    },
    mounted() {
        if (this.$refs.myIframe) {
            this.$refs.myIframe.addEventListener('load', this.highlightKeywords);
        }
        this.$refs.content.addEventListener('scroll', this.handleScroll);
        // Add a 'load' event listener to the iframe
        //this.$el.querySelector('iframe').addEventListener('load', this.highlightKeywords);
    },
    beforeUnmount() {
        // Remove the 'load' event listener from the iframe
        //this.$el.querySelector('iframe').removeEventListener('load', this.highlightKeywords);
        if (this.$refs.myIframe) {
            this.$refs.myIframe.removeEventListener('load', this.highlightKeywords);
        }
        this.$refs.content.removeEventListener('scroll', this.handleScroll);
    },
    created() {
        if (this.id) {
            this.fetchPaper(this.id)
        }
    },
    updated() {
        this.highlightKeywords();
    }
};
</script>

<style scoped>
#my-iframe {
    width: 100%;
    height: 90vh;
    border: none;
    overflow: auto;
    transform: scale(1.5);
    transform-origin: 50% 0;
}

#content {
    width: 100%;
    height: 90vh;
    overflow: auto;
    text-align: left;
}

.switch-mode-button {
    position: fixed;
    top: 4vh;
    /* adjust as needed */
    right: 10vw;
    /* adjust as needed */
    z-index: 100;
    /* ensure the button is above other content */
}
</style>