<template>
  <div>
    <!-- Filter dropdown -->
    <div style="margin-bottom: 1rem;">
      <label for="coderFilter">Filter by coder: </label>
      <select id="coderFilter" v-model="selectedCoder">
        <option value="">All coders</option>
        <option v-for="coder in uniqueCoders" :key="coder" :value="coder">
          {{ coder }}
        </option>
      </select>
    </div>

    <div class="table-container">
      <table>
        <thead>
          <tr>
            <th>Paper ID</th>
            <th>Title</th>
            <th>Type</th>
            <!-- Add more <th> elements for each property you want to display -->
          </tr>
        </thead>
        <tbody>
          <!-- Use filteredPapers instead of papers -->
          <tr v-for="paper in filteredPapers" :key="paper.paperID">
            <td>
              <a
                v-if="isClickable(paper.paperID)"
                @click="selectPaper(paper.paperID)"
                :class="{ clickable: true, selected: selectedId === paper.paperID }"
              >
                {{ paper.paperID }}
              </a>
              <span v-else>
                {{ paper.paperID }}
              </span>
            </td>
            <td>{{ paper.title }}</td>
            <td>{{ paper.type }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { read, utils } from 'xlsx';

export default {
  data() {
    return {
      papers: [],
      selectedId: null,
      selectedCoder: '' // <-- empty string = "All coders"
    }
  },
  computed: {
    // Get list of unique coder values from the data
    uniqueCoders() {
      const coders = this.papers
        .map(p => p.coder)        // assumes column is named "coder"
        .filter(c => c != null && c !== '');
      return Array.from(new Set(coders)).sort();
    },
    // Apply coder filter
    filteredPapers() {
      if (!this.selectedCoder) {
        return this.papers;
      }
      return this.papers.filter(paper => paper.coder === this.selectedCoder);
    }
  },
  methods: {
    isClickable(pdf) {
      return pdf !== 'tbd' && pdf !== 'book';
    },
    async processExcel() {
      const response = await fetch('./paper_list.xlsx');
      const arrayBuffer = await response.arrayBuffer();
      const data = new Uint8Array(arrayBuffer);
      const workbook = read(data, { type: 'array' });
      const worksheet = workbook.Sheets[workbook.SheetNames[0]];
      this.papers = utils.sheet_to_json(worksheet);
    },
    selectPaper(paperID) {
      this.selectedId = paperID;
      this.$emit('paper-select', paperID);
    },
  },
  created() {
    this.processExcel();
  }
}
</script>

<style>
.table-container {
  width: 100%;
  height: 90vh;  
  overflow: auto;  
}
table {
  min-width: 600px;  
}
.clickable {
  color: blue;
}
.selected {
  color: orange;
}
</style>
