// store.js
import { createStore } from 'vuex';
import { read, utils } from 'xlsx';

export default createStore({
  state: {
    extractedSentencesData: [],
  },
  mutations: {
    setExtractedSentencesData(state, data) {
      state.extractedSentencesData = data;
    }
  },
  actions: {
    async loadExtractedSentencesData({ commit }) {
      const response = await fetch('./extract_sentence.xlsx');
      const arrayBuffer = await response.arrayBuffer();
      const data = new Uint8Array(arrayBuffer);
      const workbook = read(data, { type: 'array' });
      const worksheet = workbook.Sheets[workbook.SheetNames[0]];
      const jsonData = utils.sheet_to_json(worksheet);
      commit('setExtractedSentencesData', jsonData);
    }
  }
});